p,
h2,
h3 {
  text-align: left;
  color: #fff;
}

.App {
  text-align: center;
  position: relative;
  height: 100vh;
  max-height: 700px;
}

.bgc {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: no-repeat url(./img/mbgc.png);
  background-size: cover;
  z-index: -1;
}

.girls {
  position: absolute;
  top: 0;
  right: 0;
  width: 36vh;
  height: 77vh;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 17px 0 0 24px;
}

.logoImg {
  width: 36px;
  height: 61px;
  margin-bottom: 55px;
}

.logo {
  margin-bottom: 10px;
  text-transform: uppercase;
}

.desc {
  font-weight: 400;
  font-size: 9px;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 120%;
  margin-bottom: 20px;
}

.main {
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: space-between;
  bottom: 5vh;
  left: 20px;
  width: 200px;
}

.bigBuble {
  background: no-repeat url(./img/buble.svg);
  background-size: cover;
  width: 90px;
  height: 95px;
  display: flex;
  margin-bottom: 15px;
  position: relative;
}

.buble {
  background: url(./img/sbuble.svg);
  background-size: cover;
  width: 25px;
  height: 25px;
  position: absolute;
  right: -25px;
  bottom: 0;
  opacity: 0.5;
}

.bubleText {
  font-style: normal;
  font-weight: 700;
  font-size: 9px;
  line-height: 11px;
  text-align: center;
  letter-spacing: 1px;
  margin: auto;
}

.content {
  display: flex;
  justify-content: space-between;
  width: 200px;
}

.text {
  font-weight: 500;
  font-size: 11px;
  line-height: 150%;
}

.bsize,
.bsoc {
  display: none;
}

.lsize,
.lsoc {
  display: block;
}

@media screen and (min-width: 700px) {
  .App {
    max-height: 1200px;
  }

  .bgc {
    background: no-repeat url(./img/bgc.png);
    background-size: cover;
  }
}

@media screen and (min-width: 1000px) {
  .girls {
    right: 25%;
  }

  .header {
    padding: 42px 0 0 64px;
  }

  .girls {
    position: absolute;
    right: 0;
    bottom: 0;
    top: auto;
    left: auto;
    max-width: 703px;
    max-height: 780px;
    width: auto;
    height: auto;
  }

  .bsize {
    display: block;
  }
  .lsize {
    display: none;
  }

  .logoImg {
    width: 55px;
    height: 92px;
    margin-bottom: 110px;
  }

  .logo {
    font-size: 44px;
  }

  .desc {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 3px;
  }

  .main {
    right: 60px;
    bottom: 35px;
    left: auto;
    width: 250px;
    height: 500px;
  }

  .bigBuble {
    width: 185px;
    height: 190px;
  }

  .bubleText {
    font-weight: 700;
    font-size: 19px;
    line-height: 22px;
  }

  .buble {
    width: 50px;
    height: 50px;
    right: auto;
    left: -10px;
    top: -30px;
    bottom: auto;
  }

  .content {
    width: 100%;
  }

  .text {
    font-weight: 500;
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .girls {
    right: 20%;
  }

  .bsoc {
    display: block;
    position: absolute;
    top: 55px;
    right: 80px;
  }

  .lsoc {
    display: none;
  }

  .logo {
    font-size: 64px;
    line-height: 74px;
    margin-bottom: 30px;
  }

  .desc {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 3px;
  }

  .main {
    width: 300px;
  }
}

@media screen and (min-height: 780px) and (min-width: 1100px) {
  .girls {
    right: 22%;
    top: auto;
    bottom: 0;
    max-width: 703px;
    max-height: 780px;
  }
}
